/**
 *  Error 
 * @param {Error|string} error
 * @returns {Error}  `message`  `msg`  Error 
 */
function transformError(error) {
    const isValid = (t) =>
        typeof t !== 'boolean' && (t === '' || t === 0 || !!t);

    let msg =
        typeof error === 'object' && isValid(error.message)
            ? error.message
            : typeof error === 'object' && isValid(error.msg)
            ? error.msg
            : typeof error === 'string'
            ? error
            : '';

    // let msg =
    //     error?.message ??
    //     error?.msg ??
    //     (typeof error === 'string' ? error : '');

    if (msg === 'Network Error') msg = '';

    if (error instanceof Error) {
        // error = err;
    } else if (typeof error === 'object') {
        error = new Error(msg);
        for (const [key, value] of Object.entries(error)) error[key] = value;
    } else {
        error = new Error(error);
    }

    error.message = msg;
    error.msg = msg;

    return error;
}

module.exports = transformError;
